






































import Vue, { PropOptions } from "vue";

import { VueMaskDirective } from 'v-mask'
import parsePhoneNumberFromString, { AsYouType, CountryCode, getExampleNumber } from 'libphonenumber-js'

import parsePhoneNumber from 'libphonenumber-js'
import countryCodes from '../static/countries.json'
import turkishCountryCodes from '../static/turkishCountry.json'
import examples from 'libphonenumber-js/mobile/examples'
import { max } from "moment";
Vue.directive('mask', VueMaskDirective);
export default Vue.extend({
  name: "QPhoneInput",
  props: {
    countryCodeAppendIcon: {
      type: String
    },
    phoneAppendIcon: {
      type: String
    },
    fullPhoneNumber: {
      type: String,
      default: '',
      note: 'Full phone number with country code included (like: +902164566565)'
    } as PropOptions,
    autoCompleteDisabled: {
      type: Boolean,
      default: false,
      note: 'disable autocomplete'
    } as PropOptions,
    dense: {
      type: Boolean,
      default: false,
      note: 'dense '
    } as PropOptions,
    textFieldDisabled: {
      type: Boolean,
      default: false,
      note: 'disable text field'
    } as PropOptions,
    required: {
      type: Boolean,
      default: false,
      note: 'is phone number required'
    } as PropOptions,
    outlined: {
      type: Boolean,
      default: false,
      note: 'is phone outlined'
    } as PropOptions,
    label: {
      type: String,
      default: 'Telefon Numarası',
      note: 'Phone input label'
    } as PropOptions,
    locale: {
      type: String,
    },
    _renderingProps: {
      type: Object,
      default: null,
    },
  },
  data() {
    const retVal = {
      defaultMask: '### ### ## ##',
      isValid: false,
      countryAlpha2Code: 'TR' as CountryCode | undefined,
      allCountries: countryCodes,
      turkishNameCountries: turkishCountryCodes,
      phoneNumber: '' as string | undefined,
      inputKey: 0,
      isProgrammaticChange: false,
      phoneData: {
        countryPhoneNumber: '',
        phoneNumber: '',
        maskedPhoneNumber: '' as string | undefined,
        mask: '',
        isValid: false,
        countryAlpha2Code: '' as CountryCode | undefined,
        countryDialCode: '' as string | null | undefined
      },
      rules: {
        required: value => {
          const stripped = (value || '').replace(/\D+/g, '');
          return stripped.length > 0 || !this.required || !!value || 'Zorunlu alanı doldurunuz .'},
        valid: value => {
          return retVal.phoneData.isValid || !value || 'Lütfen geçerli bir telefon numarası giriniz .'
        }
      },
      uniqueID: "",
      calculatedMaxLength: "" as string | null
    };
    return retVal;
  },
  watch: {
    locale: {
      immediate: true,
      handler(newValue) {
        (this.locale as any) = newValue;
        if (newValue == 'tr-TR') {
          this.allCountries = this.turkishNameCountries
        }
        else {
          this.allCountries = countryCodes
        }

      }
    },
    fullPhoneNumber: {
      immediate: true,
      handler(newValue) {
        const phoneObject = parsePhoneNumber(newValue)!
        this.countryAlpha2Code = phoneObject?.country ?? this.countryAlpha2Code;
        let nationalNumber = phoneObject?.nationalNumber ?? this.phoneNumber;
        if(this.phoneNumber === nationalNumber) return;
        this.isProgrammaticChange = true;
        this.phoneNumber = nationalNumber;
        this.inputKey += 1; 
      }
    },
    'phoneData.isValid': function () {
      (this.$refs.phoneNumber as any).validate();
    }
  },
  created() {
    this.uniqueID = this._renderingProps.compCollCb().GetCompUID();
  },
  computed: {
    getPhoneMask() {
      let countryMask: string = this.defaultMask;
      this.isValid = false;
      this.phoneData.phoneNumber = "";
      this.phoneData.countryPhoneNumber = "";
      this.phoneData.maskedPhoneNumber = "";
      if (this.phoneNumber) {
        const asYouType = new AsYouType(this.countryAlpha2Code as any);
        asYouType.input(this.phoneNumber);
        try {
          if ((asYouType as any).getTemplate()) { 
            countryMask = (asYouType as any).getTemplate().replaceAll('x', '#');
            this.phoneData.countryPhoneNumber = asYouType.getNumber()!.number;
          }
          this.phoneData.phoneNumber = (asYouType as any).digits; 
          this.isValid = asYouType.isValid();
        } catch (e) {
          countryMask = this.defaultMask;
        }
      }
      this.phoneData.mask = countryMask;
      this.phoneData.countryAlpha2Code = this.countryAlpha2Code;
      this.phoneData.countryDialCode = this.getCountryDialCode(this.countryAlpha2Code);
      this.phoneData.maskedPhoneNumber = this.phoneNumber;
      this.phoneData.isValid = this.isValid;
      this.setMaxLength(this.phoneData.countryAlpha2Code);
      this.phoneNumberInput();
      return countryMask
    }
  },
  destroyed() {
    this.$el?.remove();
  },
  methods: {
    setMaxLength(countryCode: CountryCode | undefined) {
      if (!countryCode) return;
      const examplePhoneNumber = getExampleNumber(countryCode as CountryCode, examples)?.nationalNumber;
      if (!examplePhoneNumber) return;
      const maxLength = examplePhoneNumber.length;
      let currentPhoneNumber = this.phoneData.countryPhoneNumber;
      const currentCountryDialCode = this.phoneData.countryDialCode;
      if (!currentPhoneNumber || !currentCountryDialCode) return;
      if (currentPhoneNumber.startsWith(currentCountryDialCode)) {
        currentPhoneNumber = currentPhoneNumber.slice(currentCountryDialCode.length);
      }
      currentPhoneNumber.replace(/\s+/g, '').length >= maxLength ? this.calculatedMaxLength = String(maxLength) : this.calculatedMaxLength = "";
    },
    changePhone() {
      this.$emit('change');
    },
    changeCountryCode() {
      this.$emit('changeCountryCode');
    },
    inputPhone() {
      if (this.isProgrammaticChange) {
        this.isProgrammaticChange = false;
        return; 
      }
      // this.$emit('input', this.phoneNumber);
    },
    itemText: item => item.international_name + ' (' + item.dial_code + ')',
    getCountryDialCode(countryAlpha2Code) {
      if (this.phoneNumber && this.phoneNumber.length > 0 && !this.isProgrammaticChange) {
        this.$emit('input')
      }
      const country = this.allCountries.find(e => e.code === countryAlpha2Code)
      if (country) {
        return country.dial_code
      }
      return null
    },
    phoneNumberInput() {
      this.$emit('getPhoneData', this.phoneData)
    },
    reset() {
      this.phoneNumber = "";
      this.calculatedMaxLength = "";
    },
    validate() {
  const input = this.$refs.phoneNumber as any;
  if (input && input.validate) {
    input.hasInput = true;
    input.isDirty = true;
    //input.isFocused = true;
    if(input.blur) input.blur()

    return input.validate(); // Hataları görsel olarak da tetikler
  }
  return false;
    },
    resetValidation() {
  const input = this.$refs.phoneNumber as any;
  if (input && input.resetValidation) {
    input.resetValidation(); // Vuetify metodu
    input.isDirty = false;
    input.hasInput = false;
    input.hasFocused = false;
    input.isFocused = false;
  }
}   
  }
});
